.wrapper-date-picker {
    margin: 0 auto;
    width: 480px;
    margin-top: 35px;
    background-color: #302f2f;
    height: 560px;
    margin: 0 auto;
    border-radius: 5px;
    margin-top: 20px;
}
.yearstyle {
    color: #fff;
    margin-right: 20px;
    font-size: 25px;
}
.monthstyle{
    color: #fff;
    margin-right: 10px;
    font-size: 25px;
}
.wrapper-header {
    padding-top: 25px;
    width: 420px;
    margin: 0 auto;

}
.wrapper-numberday {
    width: 420px;
    margin: 0 auto;
    margin-top: 20px;
}
.week-header {
    width: 420px;
    margin: 0 auto;
    margin-top: 60px;
}
.week-number {
    display: inline-block;
    width: 50px;
    text-align: center;
    margin: 5px;
    color: #b9babb;
}
@media only screen and (max-width: 500px) {
