@media only screen and (max-width: 350px) {
    .wrapper-date-picker {
        width: 300px;
    }
    .wrapper-numberday {
        width: 300px;
    }
    .wrapper-header {
        width: 300px;
    }
    .week-header {
        width: 300px;
    }
    .week-number {
        width: 32px;
    }
    .numberDaywithzero{
        width: 32px!important;
        height: 32px!important;
        line-height: 32px!important;
    }
    .numberDay {
        width: 32px!important;
        height: 32px!important;
        line-height: 32px!important;
    }  
    .yearstyle {
        color: #fff;
        margin-right: 20px;
        font-size: 15px;
    }
    .monthstyle{
        color: #fff;
        margin-right: 10px;
        font-size: 15px;
    }
}
