.timeHourOut {
    position: absolute;
    top: 30%; 
    left: calc(50% - 3px); 
    width: 0;
    height: 260px;
    display: inline-block;
    text-align: center;
}

.numberTime:hover {
    background-color: #da4335;
    border-radius: 50%;
}
.timeHourIn {
    position: absolute;
    left: 20%;
    top: calc(30% + 40px); 
    left: calc(50% - 3px); 
    width: 0;
    height: 180px;
    display: inline-block;
    text-align: center;
}
.screen {
    position: absolute;
    top: 12%; 
    left: calc(50% - 50px); 
    font-size: 50px;
    width: 20px;
    height: 350px;
    display: inline-block;
    text-align: center;
}
.wrapper-clock {
    margin: 0 auto;
    background-color: #2f2f2f;
    width: 350px;
    margin-top: 45px;
    height: 520px;
    border-radius: 5px;
}
.buttonOk {
    color: #b13e34;
    background-color: #2f2f2f;
    border: none;
    font-weight: 600;  
}
.buttonCancel {
    margin-top: 460px;
    background-color: #2f2f2f;
    border: none;
    font-weight: 600;
    text-transform: uppercase;
    color: #b13e34;
    margin-left: 150px;
    margin-right: 30px;
}
@media only screen and (max-width: 360px) {
    .wrapper-clock {
        width: 315px;
    }
 }
 @media only screen and (max-width: 320px) {
    .wrapper-clock {
        width: 280px;
       
    }
    .timeHourOut {
        height: 245px!important;
    }
 }

